.services {
	.about-more {
		padding-top: 80px;
		padding-bottom: 80px;
		@include desktops {
			padding-top: 100px;
			padding-bottom: 100px;
		}
	}
}

.service_single-area {
	padding-top: 80px;
	padding-bottom: 80px;
	@include desktops {
		padding-top: 100px;
		padding-bottom: 100px;
	}
	.service_details-desc {
		h2 {
			margin-top: 40px;
			text-transform: capitalize;
		}
		p {
			margin-top: 15px;
		}
		ul.styled_list {
			margin-top: 15px;
			i {
				color: $primary-color;
			}
		}
		h4 {
			margin-top: 40px;
		}
	}
	.skill, .faq {
		padding-top: 30px;
		.section-title {
			h2 {
				font-size: 24px;
			}
		}
		.progress-holder {
			margin-top: 30px;
		}
	}
}
.sidebar {
	.list-group {
		margin-bottom: 40px;
		@include tablet {
			margin-bottom: 0;
		}
		.list-group-item {
			margin-top: 0px;
			&:first-child {
				margin-top: 0px;
			}
			a {
				font-size: 14px;
				font-weight: 400;
				position: relative;
				color: $title-color;
				padding-left: 0px;
				&:before {
					position: absolute;
					content: '';
					font-family: 'FontAwesome';
					left: 0
				}
				&:hover {
					color: $primary-color;
				}
			}
			&.active {
				a {
					color: $white-color;
				}
			}
		}
	}
}
